.analysis-container {
    width: 80vw;
}

.buttons {
    height: 60px;
    align-content: space-evenly;
    text-align: center;
}

.nav-button {
    color: #FFFFFF;
    width: 250px;
    font-size: 24px;
    font-weight: 100;
    padding: 4px 8px;
    background-color: #5296DD;
    border: solid #337FED 1px;
    text-decoration: none;
    display: inline-block;
    cursor: pointer;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    margin-right: 40px;
}

.nav-button:hover {
   background: #1E62D0;
   border: solid #5296DD 1px;
   -webkit-border-radius: 20px;
   -moz-border-radius: 20px;
   border-radius: 20px;
   text-decoration: none;
}
.nav-button:focus {
    outline: none;
}

img {
    width: 80%;
    display: block;
    margin-left: auto;
    margin-right: auto;
}
