.intro p {
    font-size: 18px;
}

.graph {
    position: relative;
    width: 40%;
    border-style: solid;
    border-color: #92bddf;
    margin: 0 auto;
}

.graph > .info {
    position: absolute;
    top: 0;
    left: 40vw;
    width: 200px;
    height: fit-content;
    border-style: solid;
    border-color: #99BEEB;
    border-radius: 5px;
    padding: 15px;
}

.graph > div > svg {
    width: 100% !important;
}

.header {
    width: fit-content;
}

hr {
    border: 2px solid #F3723F;
}